import React from 'react';

export default ({ style }) => (
  <div style={{ width: '200px', height: 'auto', ...style }}>
    <img 
      src="images/logo.png" 
      style={{ width: '100%' }}
      alt="heart_logo_for_potato_head_project"
    />
  </div>
);


